"useClient";
type Lesson = {
  title: string;
  duration: number;
};
export default function Counter() {
  const lesson: Lesson = { title: "TipeScript", duration: 120 };
  const count = calculateSum(5, 10);

  function calculateSum(a: number, b: number) {
    return a + b;
  }

  return (
    <div className="flex flex-col gap-4">
      <CoolCount count={count} />
      <button
        onClick={() => {
          console.log(count);
        }}
      >
        Увеличить
      </button>
    </div>
  );
}
type Props = {
  count: number;
};

function CoolCount({ count }: Props) {
  return <span className="text-#ff0303-500">{count}</span>;
}
